import { NUI, MouseEventData } from '../../lib';
import { ViewController } from '../index';
export class NUIWrapper {
    nui: NUI;
    viewController: ViewController;
    constructor(viewController: ViewController, needUI: boolean) {
        this.viewController = viewController;
        this.nui = new NUI(this.viewController.app.model.modelViewCoreWrapper.viewWrapper.div, needUI);
        this.nui.BindingActiveCommandController('NTF', this.viewController.app.model.controllerCoreWrapper.activeCommandController);
    }
    resize(width: number, height: number) {
        this.nui.Resize(width, height);
    }
    mouseInteractionMenu(m: MouseEventData) {
        this.nui.MouseInteractionMenu(m);
    }
    initPullDown() {
        this.nui.pullDownMenuWrapper.menus[1].AddApp('mlProject@TFModel01');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('mlProject@TFModel02XOR');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('mlProject@TFModel03Iris');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('mlProject@--');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('mlProject@TFModel03SmartDrawing');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('mlProject@TFModel01HorsepowerAndMilesPerGallon');

        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@TFPlayground01');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@JSLinearRegression');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@JSLinearRegressionGD');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@TFLinearRegressionGD');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@--');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@TFPolynomialRegression GD');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@TFPolyRegress Interaction');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@TFPolyRegress Miles/Gallon');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@--');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@TF Regression');

        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@--');
        this.nui.pullDownMenuWrapper.menus[1].AddApp('tfProject@TF Regression');
    }
    destroy() {
        this.nui.Destroy();
    }
}