import * as _enum from './lib';
import { Model } from './Model';
import { Implementation } from './Implementation';
import { ViewController } from './ViewController';

export class AppTFEnv {
    viewController: ViewController;
    model: Model;
    implementation: Implementation;
    needUI: boolean = false;
    divHost: HTMLElement;
    public static InitSubApp(id: string, subApp: string) {
        const app = new AppTFEnv(id, false);
        app.implementation.InitProjects().then(() => {
            app.implementation.tfProject.LoadDefinition(subApp);
        });
        // app.implementation.LoadDefinition('creativeCoding', subApp);
    }
    public static Init(id: string) {
        new AppTFEnv(id);
    }
    constructor(id: string, needUI: boolean = true) {
        this.needUI = needUI;
        const divHost = document.getElementById(id);
        if (divHost) { this.divHost = divHost; }

        this.model = new Model(this, id);
        this.implementation = new Implementation(this);
        this.viewController = new ViewController(this, needUI);

        console.log('=-=-=-=-=-=-=')
    }
}

export class Solution {
    public divHost: HTMLElement;
    constructor(id: string ) {
        this.divHost = document.getElementById(id);
        AppTFEnv.Init(id)
    }
    public destroy() {
        if (this.divHost) {
            try {
                while (this.divHost.firstChild) {
                    this.divHost.removeChild(this.divHost.firstChild);
                }
            } catch (error) {
                console.error('Error in destroy method:', error);
            }
        } else {
            console.warn('divHost is undefined in destroy method');
        }
    }
}